.wp-block-zolo-counter {
    --zolo-item-padding: clamp(2.5rem, 5vw, 3.5rem);
    --zolo-item-gap: 15px;
    --zolo-count-marign: 0 0 15px;
    --zolo-count-text-color: #20232a;
    --zolo-icon-size: 30px;
    --zolo-icon-color: var(--zoloblocks-brand-color);
    --zolo-title-text-color: #717274;
    --zolo-title-margin: 0;
    --zolo-icon-margin: 0;
    --zolo-icon-svg-margin: 0 0 0;

    .zolo-counter-item {
        padding: var(--zolo-item-padding);
        box-sizing: border-box;
        overflow: hidden;
        background-color: var(--zoloblocks-item-bg-color);
        border-width: var(--zolo-item-border-width);
        border-style: var(--zolo-item-border-style);
        border-color: var(--zoloblocks-item-border-color);
        border-radius: var(--zolo-item-border-radius);
        text-align: center;
    }

    .zolo-counter-count {
        font-size: 40px;
        font-weight: 700;
        line-height: 1;
        color: var(--zolo-count-text-color);
        margin: var(--zolo-count-marign);
        display: inline-block;
    }

    .zolo__display-icon {
        margin-bottom: 15px;
        display: inline-block;
    }

    .zolo-counter-icon {
        img {
            max-width: 100%;
            box-sizing: border-box;
            vertical-align: middle;
        }
        svg {
            fill: var(--zolo-icon-color);
            box-sizing: content-box;
            width: var(--zolo-icon-size);
            height: var(--zolo-icon-size);
            display: block;
        }
    }

    .zolo-counter-title {
        font-size: 18px;
        color: var(--zolo-title-text-color);
        margin: var(--zolo-title-margin);
        display: block;
    }

    .zolo-counter-wrap {
        &.style-1,
        &.counter-align-right {
            .zolo-counter-item {
                display: flex;
                align-items: flex-start;
                gap: var(--zolo-item-gap);
                text-align: left;
            }
            .zolo-counter-icon {
                margin: var(--zolo-icon-margin);
                svg {
                    margin-bottom: var(--zolo-icon-svg-margin);
                }
            }
        }
        &.counter-align-right {
            .zolo-counter-item {
                flex-direction: row-reverse;
                text-align: right;
            }
        }
    }
}
